.checkbox {
  background-color: red;
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  cursor: pointer;
}
.checkbox label input[type=checkbox] {
  background-color: red;
}
.checkbox input {
  display: none;
}
.checkbox .checkbox__icon {
  width: 20px;
  height: 20px;
  border: 1px solid #000;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 10px;
}
.checkbox .checkbox__icon svg {
  display: none;
}
.checkbox .checkbox__label {
  font-size: 14px;
  color: #000;
}
.checkbox.checked .checkbox__icon {
  background-color: #000;
}
.checkbox.checked .checkbox__icon svg {
  display: block;
  fill: #fff;
}